import { useState } from "react";
import { blogCard } from "@/data";
import Image from "next/image";

export default function Blog() {
  // Explicitly define the type of hoveredIndex as 'number | null'
  const [hoveredIndex, setHoveredIndex] = useState<number | null>(null);

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 mt-10 items-center justify-center lg:px-28 lg:py-10 mb-24">
      {blogCard.map((card, index) => (
        <div
          className="flex flex-col gap-4 border border-transparent rounded-2xl transition-transform duration-300 w-80" // Fixed width for uniformity
          key={index}
          onMouseEnter={() => setHoveredIndex(index)} 
          onMouseLeave={() => setHoveredIndex(null)} 
        >
         
          <div className="overflow-hidden rounded-2xl">
            <Image
              src={card.img}
              alt={card.title}
              width={330}
              height={100}
              className={`rounded-2xl h-52 transition-transform duration-300 ${hoveredIndex === index ? 'scale-110' : ''}`}
            />
          </div>

          {/* Text Div */}
          <div className="flex flex-col gap-3 border-white border-l-2">
            <div className="px-5 flex flex-col gap-8">
              <div className="flex flex-col">
                <h2 className="text-white font-semibold text-lg">{card.title}</h2>
                <div className="flex flex-row items-center gap-2 text-white opacity-80">
                  <span>{card.span1}</span>
                  <div className="h-2 w-2 rounded-full bg-white mx-[6px]"></div>
                  <span>{card.date}</span>
                </div>
                <h3 className={`text-white font-semibold text-[23px] mt-4 transition-transform duration-300 ${hoveredIndex === index ? 'scale-90' : ''}`}>
                  {card.span2}
                </h3>
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}
